
@import './../../common/styles/sass/variables';

.project {
  display: flex;
  border: 1px solid $primaryColor;
  width: 400px;
  max-height: 350px;
  flex-direction: column;
  align-self: stretch;
  flex-wrap: nowrap;

  .image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80%;
    background-color: #3c3b3b;
    background-size: cover;
    background-position: center;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.2;
    }

    .viewBtn {
      position: relative;
      display: flex;
      padding: 10px 20px;
      width: 200px;
      height: 200px;
      justify-content: center;
      align-items: center;
      color: coral;
      font-size: large;
      font-weight: bold;
      z-index: 1;
    }
  }

  .projectInfo {
    display: flex;
    height: 20%;
    padding: 5px 15px;
    flex-direction: column;
    align-items: flex-start;
    background-color: $bgColorDark;

    .projectTitle {
      height: 30%;
      font-family: 'Josefin Sans', sans-serif;
      font-size: 16px;
      letter-spacing: 1px;
      margin: 0 0 5px;
      color: $textColor;
      line-height: 1.2;
    }

    .projectDescription {
      height: 50%;
      font-family: 'Poppins', sans-serif;
      font-weight: 200;
      font-size: 12px;
      letter-spacing: 1px;
      margin: 5px 0 0;
      color: $textColor;
      line-height: 1.8;
    }
  }
}










